.footer {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    background-color: $footer-background-color;
    padding: 30px 0;
    margin-top: 100px;

    @include mobile-only {
        margin-top: 50px;
    }

    &__img {
        width: 120px;
        margin: 30px 0;
    }

    &__text {
        color: white;
        font-weight: 500;
        font-size: 24px;

        @include mobile-only {
            font-size: 12px;
            margin-bottom: 30px;
        }
    }
}
